import React, { useState } from 'react';
import { useNavigate, useParams } from 'react-router-dom'
import '../styles/NavBar.css'
import LoginPage from './LoginPage.js';

function NavBar() {
  let navfunction = useNavigate();
  const routeChange = param => () => {navfunction(param)}
  const [isLoginDisplay, setIsLoginDisplay] = useState(false);

  return (
    <div id='navbar'>
      <div style={{display:'flex',width:'100%'}}>
        <div style={{width:'20%'}}>
          <a href='/'><img id='logo-style' src={require('../assets/JP Movies_Logo_Long.png')}/></a>
        </div>
        <div className='navbarwordings'>
          <div className='menuSpacing'><button onClick={routeChange('/jp_movies_web')}>HOME</button></div>
          <div className='menuSpacing'><button onClick={routeChange('/jp_movies_web/movies')}>MOVIES</button></div>
          <div className='menuSpacing'><button onClick={routeChange('/jp_movies_web/cinema')}>CINEMAS</button></div>
          <div className='menuSpacing'><button onClick={routeChange('/jp_movies_web/promotions')}>PROMOTION</button></div>
        </div>
        <div style={{width:'20%'}}>
          <button id='login-button' onClick={() => setIsLoginDisplay(true)}>Login</button>
          <LoginPage isLoginDisplay={isLoginDisplay} setIsLoginDisplay={setIsLoginDisplay}></LoginPage>
        </div>
      </div>
    </div>
  )
}

export default NavBar