import './App.css';
import { BrowserRouter, Route, Routes } from 'react-router-dom';
import homePage from './pages/homePage';
import cinema from './pages/cinema';
import promotions from './pages/promotions';
import loginpage from './pages/loginpage';
import movies from './pages/movies';
import signup from './pages/signup';

function App() {
  return (
    <div className="App" style={{backgroundColor:'black'}}>
      <BrowserRouter>
      <Routes>
        <Route exact path='/jp_movies_web' Component={homePage}></Route>
        <Route exact path='/jp_movies_web/cinema' Component={cinema}></Route>
        <Route exact path='/jp_movies_web/promotions' Component={promotions}></Route>
        <Route exact path='/jp_movies_web/loginpage' Component={loginpage}></Route>
        <Route exact path='/jp_movies_web/movies' Component={movies}></Route>
        <Route exact path='/jp_movies_web/signup' Component={signup}></Route>
      </Routes>
      </BrowserRouter>
    </div>
  );
}

export default App;
